export default function validate(values) {
    let errors = {};
    //validate name
    if (!values.username)
        errors.username = 'Tn ng nhp khng c  trng !';

    //validate password
    if (!values.password)
        errors.password = 'Mt khu khng c  trng !';
    else if (values.password.length < 4)
        errors.password = 'Mt khu khng t hn 4 k t !';
    return errors;
}