import React, {useEffect, useState} from 'react';
import swal from 'sweetalert';
import './login.scss';
import {Button, Form} from "react-bootstrap";
import validate from "../validate/validateLogin";
import useForm from "../helpers/userForm";
import {login} from "../actions/rootAction";
import {useDispatch, useSelector} from 'react-redux';
import {useHistory} from 'react-router-dom';
import {AxiosAdBe, AxiosUsBe} from "../utils/axios";

const Login = () => {
    let [resErr, setResErr] = useState('');
    let [saveLogin, setSaveLogin] = useState(false);
    let user = useSelector(state => state.main.user);
    const dispatch = useDispatch();
    const history = useHistory();

    const stateSchema = {
        username: '',
        password: ''
    };
    useEffect(_ => {
        if (Object.keys(user).length !== 0)
            history.push('/')
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [user])
    const {handleChange, handleSubmit, values, errors} = useForm(stateSchema, submit, validate);

    function submit() {
        dispatch(login({username: values.username, password: values.password}))
            .then(({data: res}) => {
                try {
                    if (saveLogin){
                        let {token} = res.data.user;
                        AxiosUsBe.defaults.headers.common = {'Authorization': `Bearer ${token}`}
                        AxiosAdBe.defaults.headers.common = {'Authorization': `Bearer ${token}`}
                        localStorage.setItem('token', token)
                    }
                } catch (e) {
                    swal({
                        title: "C li xy ra trong qu trnh ng nhp",
                        icon: "fail",
                        button: false,
                        timer: 1500
                    }).then(r => r)
                }
                if (res.success) {
                    swal({
                        title: res.message,
                        icon: "success",
                        button: false,
                        timer: 1500
                    }).then(r => r)
                    history.push('/')
                } else {
                    setResErr(res.message)
                }
            })
            .catch(err => {
                swal({
                    title: "C li xy ra trong qu trnh ng nhp",
                    icon: "fail",
                    button: false,
                    timer: 1500
                }).then(r => r)
            })
    }

    return (
        <div className={'login'}>
            <div className="login__form">
                <Form className={'px-4 py-5'}>
                    <Form.Group>
                        <h1 style={{color: '#5d78ff'}} className={'text-center text-uppercase mb-5'}>Small Code CMS</h1>

                        {resErr && <div className="alert alert-danger" role="alert">
                            {resErr}
                        </div>}
                        <Form.Label>Tn ng nhp</Form.Label>
                        <Form.Control name={'username'} type="text" placeholder="Tn ng nhp ..."
                                      onChange={handleChange}
                                      value={values.username}/>
                        {
                            errors['username'] ? <Form.Text className="error">
                                {errors['username']}
                            </Form.Text> : null
                        }
                    </Form.Group>

                    <Form.Group>
                        <Form.Label>Mt khu</Form.Label>
                        <Form.Control name={'password'} type="password" placeholder="@mt khu ..."
                                      onChange={handleChange} value={values.password}/>
                        {
                            errors['password'] ? <Form.Text className="error">
                                {errors['password']}
                            </Form.Text> : null
                        }
                    </Form.Group>
                    <Form.Group>
                        <Form.Check value={saveLogin} style={{fontSize: '15px'}} className={'text-muted'} type="checkbox" onChange={(e) => setSaveLogin(e.target.value)}
                                    label="Nh ti khon"/>
                    </Form.Group>
                    <Button onClick={handleSubmit} style={{background: '#5d78ff'}} type="submit" block={true}>
                        ng nhp
                    </Button>
                </Form>
            </div>
        </div>
    );
};

export default Login;