import React, {forwardRef, useImperativeHandle, useState} from 'react';
import {Button, Form, Modal, Row} from "react-bootstrap";
import Col from "react-bootstrap/Col";
import Editor from "../editor/Editor";
import useForm from "../../helpers/userForm";
import {AxiosUsBe} from "../../utils/axios";
import qs from 'querystring';
import {useSelector} from "react-redux";
import swal from "sweetalert";

const ModalUpdateExercise = forwardRef(({course,quiz,edit}, ref) => {
    const user = useSelector(state=>state.main.user);
    const [show, setShow] = useState(false);
    const [code, setCode] = useState(``);
    const [mainCode, setMainCode] = useState(``);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const stateSchema = {
        title: quiz.title,
        questionSen: quiz.question,
        tutorial: quiz.tutorial,
        result: quiz.result
    };

    const validate = () => {
        let errors = {};
        //validate email
        if (!values.title)
            errors.title = 'Tiu  khng c  trng !';

        if (!values.questionSen)
            errors.questionSen = 'Cu hi khng c  trng !';
        //validate password
        if (!values.result)
            errors.result = 'Kt qa khng c  trng !';

        return errors;
    }

    const submit = () => {

        let payload = {
            levelId: quiz.levelId,
            kindChallengeId: 2,
            title: values.title,
            sequenceNumber: quiz.sequenceNumber,
            questionSen: values.questionSen,
            tutorial: values.tutorial,
            result: values.result,
            courseId: course.id,
            languageId: course['LanguageChallenges'][0]['id'],
            authorId: user.id,
            code: `${code}`,
            mainCode: `${mainCode}`,
            active:1
        }
        AxiosUsBe.put(`/api/quiz/${quiz.id}`, qs.stringify(payload))
            .then(({data:res}) => {
                handleClose();
                edit({...res.data,Courses:quiz['Courses']},2)
                resetForm();
                swal({
                    title: 'Chnh sa thnh cng  !',
                    icon: 'success',
                    button: false,
                    timer: 1500
                }).then(r => r)

            })
            .catch(err => {
                handleClose()
                swal({
                    title: 'C li xy ra trong qu trnh x l !',
                    icon: 'error',
                    button: false,
                    timer: 1500
                }).then(r => r)
            })

    }

    const {handleChange, handleSubmit, values, errors,resetForm} = useForm(stateSchema, submit, validate);

    useImperativeHandle(ref, () => {
        return {
            show: handleShow
        };
    });


    return (
        <Modal show={show} onHide={handleClose} dialogClassName="modal-90w">
            <Modal.Header closeButton>
                <Modal.Title>
                    <h1 className={'title'}>Chnh sa</h1>
                </Modal.Title>
            </Modal.Header>
            <Modal.Body>
                <Row>
                    <Col sm={6}>
                        <Form>
                            <Form.Row>
                                <Col>
                                    <Form.Label>Tiu </Form.Label>
                                    <Form.Control placeholder="Nhp tiu  ..." name={'title'} value={values.title}
                                                  onChange={handleChange}/>
                                    {
                                        errors['title'] ? <Form.Text className="error">
                                            {errors['title']}
                                        </Form.Text> : null
                                    }
                                </Col>
                            </Form.Row>
                            <Form.Row>
                                <Col>
                                    <Form.Label>Bi</Form.Label>
                                    <Form.Control value={quiz.levelId} disabled={true}/>
                                </Col>
                                <Col>
                                    <Form.Label>Th t</Form.Label>
                                    <Form.Control value={quiz.sequenceNumber} disabled={true}/>
                                </Col>
                            </Form.Row>
                            <Form.Row className={'mt-2'}>
                                <Col>
                                    <Form.Label>Cu hi </Form.Label>
                                    <Form.Control name={'questionSen'} rows={3} as={'textarea'}
                                                  value={values.questionSen}
                                                  placeholder="Nhp cu hi ..." onChange={handleChange}/>
                                    {
                                        errors['questionSen'] ? <Form.Text className="error">
                                            {errors['questionSen']}
                                        </Form.Text> : null
                                    }
                                </Col>
                            </Form.Row>

                            <Form.Row className={'mt-2'}>
                                <Col>
                                    <Form.Label>Hng dn</Form.Label>
                                    <Form.Control name={'tutorial'} rows={5} as={'textarea'}
                                                  value={values.tutorial}
                                                  placeholder="Nhp hng dn ..." onChange={handleChange}/>
                                    {
                                        errors['tutorial'] ? <Form.Text className="error">
                                            {errors['tutorial']}
                                        </Form.Text> : null
                                    }
                                </Col>
                            </Form.Row>
                            <Form.Row className={'mt-2'}>
                                <Col>
                                    <Form.Label>Kt qu</Form.Label>
                                    <Form.Control name={'result'} value={values.result} placeholder="Nhp kt qu ..."
                                                  onChange={handleChange}/>
                                    {
                                        errors['result'] ? <Form.Text className="error">
                                            {errors['result']}
                                        </Form.Text> : null
                                    }
                                </Col>
                            </Form.Row>
                        </Form>
                    </Col>
                    <Col sm={6} style={{height: '70vh',overflowY: 'auto'}}>
                        <div className={'sticky-top'}>
                            <Form.Label>Code</Form.Label>
                            <Editor code={quiz.code} type={course['LanguageChallenges'][0]['title']} change={(code) =>setCode(`${code}`)}/>
                            <Form.Label className={'mt-4'}>Main Code</Form.Label>
                            <Editor code={quiz.mainCode||''} type={course['LanguageChallenges'][0]['title']}
                                    change={(mainCode) => setMainCode(`${mainCode}`)}/>
                        </div>
                    </Col>
                </Row>
                <Row className={'mt-4'}>
                    <Col sm={{span: 4}}>
                        <Button onClick={handleSubmit} variant={'success'} block={true}>Chnh sa</Button>
                    </Col>
                </Row>
            </Modal.Body>
        </Modal>
    );
});

export default ModalUpdateExercise;